<template>
	<div @click="clickBox" class="box">
		<div class="head clearFloat nav-bg">
			<div class="logo">
				<img src="../assets/image/logo.png"/>
				{{$t('home.logo')}}
			</div>
			<ul class="nav">
				<li v-for="(v,index) in navList" @mouseenter="enterShowMenu(index)" @mouseleave="leaveHideMenu"  :class="{active:navCurIndex == index || hoverIndex == index}">
					<a @click="changeActive(index)" href="javascript:;">
						{{ v.title }}
						<!-- <span v-if="v.children != undefined && hoverIndex != index" class="el-icon-arrow-down"></span>
							<span v-if="v.children != undefined && hoverIndex == index" class="el-icon-arrow-up"></span> -->
						</a>
						<!-- <ul v-if="v.children != undefined && hoverIndex == index" class="menu">
							<li v-for="(item,key) in v.children"  @click="menuChangeActive(key,index)" :class="{'menu-active':menuCurIndex == key}">
								<a href="javascript:;">{{ item.title }}</a>
							</li>				
						</ul> -->
					</li>
				</ul>
			<!-- <div class="search-bar">
				<input class="search-input" type="text" placeholder="请输入车牌号/设备号" v-model="searchInput"/>
				<a href="javascript:;" class="search-btn el-icon-search" ></a>
			</div> -->
			<div class="my-name">
				<img src="../assets/image/myIcon.png"/>
				<span class="name">{{ userName }}</span>
			</div>
			<el-badge :value="msgNum" :max='99' class="item info-num-msg">
				<a class="el-icon-bell" herf="javascript:;"></a>
			</el-badge>
			<a v-show="isEN == 'cn'" @click="reviseEN" class="text-type" href="javascript:;">EN</a>
			<a v-show="isEN == 'en'" @click="reviseCN" class="text-type" href="javascript:;">中文</a>
			<!-- <div class="info-num-msg">
				<span class="num">{{ msgNum }}</span>
				<a class="el-icon-bell" herf="javascript:;"></a>
			</div> -->
			<!-- <div class="right">
				<span class="user">用户</span>
				<span @click="changeQuit" class="quit">退出</span>
			</div> -->
		</div>
		<router-view></router-view>
	</div>
</template>
<script>
import store from '@/store'
export default {
	data() {
		return {
			navList: [
			{
				title: '首页'
			},
			{
				title: '车辆监控',
				children: [
				{
					title: '实时监控'
				},
				{
					title: '轨迹分析'
				}
				]
			},
			/*{
				title: '运营分析'
			},*/
			{
				title: '轨迹分析'
			},
			{
				title: '数据查询'
			},
			{
				title: '车辆管理'
			},
			{
				title: '系统管理'
			}
			],
			hoverIndex: null,
			navCurIndex: '0',
			searchInput: '',
			menuShow: false,
			msgNum: 12,
			userName: '深圳市开源节流科技有限公司',
			menuCurIndex: '0',
			isEN: ''
		}
	},
	created() {
		this.getPath();
		this.isEN = localStorage.lang == undefined ? 'cn' : localStorage.lang;
	},
	mounted() {
		

	},
	methods: {
		changeActive(index) {
			this.navCurIndex = index;
			switch (index){
				case 0:
				this.$router.push('/home/homePage');
				break;
				case 1:
				this.$router.push('/home/carMonitor/allMonitor');
				break;
				case 2:
				this.$router.push('/home/carMonitor/carAnalysis');
				break;
				case 3:
				this.$router.push('/home/dataSearch');
				break;
				case 4:
				this.$router.push('/home/carManag');
				break;
				case 5:
				this.$router.push('/home/sysetsmM');
				break;
				default:
			}
		},
		//鼠标移入显示子菜单
		enterShowMenu(index) {
			this.hoverIndex = index;
		},
		leaveHideMenu() {
			this.hoverIndex = null;
		},
		menuChangeActive (key,index) {
			this.navCurIndex = index;
			this.menuCurIndex = key;
			if(index == '1' && key == '0') {
				this.$router.push('/home/carMonitor/allMonitor');
			} else if(index == '1' && key == '1') {
				this.$router.push('/home/carMonitor/carAnalysis');
			}
		},
		changeQuit(){
			this.$confirm('确定退出登录?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				window.sessionStorage.removeItem('token');
				this.$store.state.token = '';
				this.$router.push({path:'/login'});
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消退出'
				});          
			});
			
		},
		getPath(){//监听路由
			var routeName = window.location.hash;
			if(routeName.indexOf('/home/homePage') > -1) {
				this.navCurIndex = '0';
			} else if(routeName.indexOf('/home/carMonitor/allMonitor') > -1) {
				this.navCurIndex = '1';
			} else if(routeName.indexOf('/home/carMonitor/carAnalysis') > -1) {
				this.navCurIndex = '2';
			} else if(routeName.indexOf('/home/dataSearch') > -1) {
				this.navCurIndex = '3';
			} else if(routeName.indexOf('/home/carManag') > -1) {
				this.navCurIndex = '4';
			} else if(routeName.indexOf('/home/sysetsmM') > -1) {
				this.navCurIndex = '5';
			}
		},
		clickBox(){
			this.$store.state.treeShow = false;
		},
		//切换英文
		reviseEN(){
        	localStorage.setItem('lang','en');
			this.$i18n.locale = 'en';
			this.isEN = 'en';
		},
		//切换中文
		reviseCN(){
        	localStorage.setItem('lang','cn');
			this.$i18n.locale = 'cn';
			this.isEN = 'cn';
		}
	},
	store,
	watch: {
		'$route': 'getPath'
	}
}
</script>

<style scoped>
.box {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.box .head{
	padding: 0 20px 0 70px;
	height: 66px;
	/*height: 80px;*/
}

.box .head .logo {
	float: left;
	height: 26px;
	line-height: 26px;
	font-size: 26px;
	color: #00a8d4;
	margin-top: 20px;
}
.box .head .logo img{
	width: 49px;
	height: 26px;
	display: block;
	float: left;
	margin-right: 10px;
}

.box .head .nav{
	float: left;
	height: 66px;
	margin-left: 70px;
}
.box .nav li{
	float: left;
	height: 66px;
	font-size: 16px;
	line-height: 66px;
	margin-left: 60px;
	position: relative;
}
.box .nav a{
	display: block;
}
.box .nav .active a,.box .nav li:hover a{
	color: #00a8d4;
}
.box .menu{
	height: 40px;
	position: absolute;
	top: 66px;
	left: 0;
	z-index: 999;
	width: 124px;
	padding: 0 10px;
	background: #1c2327;
}
.box .nav .menu li{
	font-size: 14px;
	margin: 0 10px 0 0;
	text-align: center;
	float: left;
}
.box .nav .menu li:last-child{
	margin: 0;
	float: right;
}
.box .nav .menu li a{
	color: #fff;
	line-height: 40px;
	display: block;
}
.box .nav .menu .menu-active a,.box .nav .menu li:hover a{
	color: #00a8d4;
}
.search-bar{
	float: left;
	position: relative;
	margin: 22px 0 0 10px;
}
.search-bar .search-input{
	width: 200px;
	padding: 0 50px 0 15px;
	height: 36px;
	border: none;
}
.search-bar .search-btn{
	width: 34px;
	height: 34px;
	line-height: 34px;
	text-align: center;
	position: absolute;
	top: 1px;
	right: 1px;
	background: #293342;
	color: #fff;
	font-size: 20px;
	border: none;
}
.info-num-msg{
	float: right;
	position: relative;
	margin: 24px 30px 0 0;
	color: #fff;
	cursor: pointer;
}
.info-num-msg a{
	display: block;
	position: relative;
	left: 8px;
	top: -4px;
}
.el-icon-bell{
	display: block;
	font-size: 30px;
}
.my-name{
	overflow: hidden;
	float: right;
	margin: 19px 0 0;
}
.my-name img{
	float: left;
	width: 28px;
	height: 28px;
	margin-right: 10px;
}
.my-name .name{
	display: block;
	float: left;
	font-size: 14px;
	color: #fff;
	line-height: 28px;
}
.box .head .right {
	float: right;
	overflow: hidden;
}

.box .head .right span {
	float: left;
	display: block;
	color: #fff;
	font-size: 12px;
	line-height: 70px;
	margin-left: 10px;
}
.box .head .right .quit{
	cursor: pointer;
}
.text-type{
	float: right;
	font-size: 14px;
	color: #00A8D4;
	margin: 21px 6px 0 0;
	display: block;
	line-height: 24px;
}
</style>


